import React from 'react';


// classes
import classes from './App.module.css';


// async component, || lazy component 

import lazy from '../hoc/AsyncComponent';





// REACT router 
import {Switch,Route,NavLink,HashRouter, Link} from 'react-router-dom';



// material ui 


//layout 
import SideDrawer from '../Components/UI/SideDrawer/SideDrawer';
import Backdrop from '../Components/UI/Backdrop/Backdrop';


import {Grid,Button} from '@material-ui/core'




const ProjectAsyncComponent = lazy(() => import('../Containers/Project/Project'));
const SkillAsyncComponent = lazy(()=> import('../Containers/Skill/Skill'));
const IntroAsynccomponent = lazy(()=> import('../Containers/Introduction/Introduction'));

const ContactAsyncComponent = lazy(()=> import('../Containers/Contact/Contact'))





class App extends React.Component {


    state = {
        show : false 
    }
   
    clickHandler = () => {
        this.setState(prevState => ({show : !prevState.show}))
    }





    render(){
        
        return (
            <HashRouter basename={process.env.PUBLIC_URL} >

            <Backdrop show={this.state.show} clicked={this.clickHandler}/>

            <SideDrawer clicked={this.clickHandler} show={this.state.show}>
          
                </SideDrawer>
             

            
            

            <div className={classes.App}>

     
            <Grid className={classes.nav} container>

            <Grid item xs={10} className={classes.menu}>
                <Button onClick={this.clickHandler} >
                    ---
                </Button>
            </Grid>

            <Grid className={classes.Logo} item xs={2} md={6} sm={6}>
            phd
            
            </Grid>
           

            <Grid className={classes.buttons} item xs={12} md={6} sm={6}>
               
            <div className={classes.buttonss}>

            <NavLink
            activeStyle={{
                borderBottom : '3px solid orange'
            }}

            exact

            to="/">
                <Button>
                    Introduction
                </Button>
            </NavLink>

            <NavLink 
             activeStyle={{
                borderBottom : '3px solid orange'
            }}
            to="/skill">
            
                <Button>
                    Skill
                </Button>
            </NavLink>

            <NavLink
             activeStyle={{
                borderBottom : '3px solid orange'
            }}
            exact
            to="/project">
            
                <Button>
                    Project
                </Button>
            </NavLink>
            <NavLink
             activeStyle={{
                borderBottom : '3px solid orange'
            }}
            to="/contact">
            
                <Button>
                    Contact
                </Button>
            </NavLink>

            </div>


            </Grid>

            </Grid>
           



            <Switch>

            
            <Route path="/" exact component={IntroAsynccomponent}/>
            <Route path="/skill"  render={() => <SkillAsyncComponent/>}/>
            <Route path="/project" render={() => <ProjectAsyncComponent/>}/>
            <Route path="/contact" component={ContactAsyncComponent}/>
            </Switch>
            </div>
            </HashRouter>
        )
    }
};


export default App ;