import React, {Component} from 'react';

import {CircularProgress} from '@material-ui/core'

import Aux from './Aux';

const AsyncComponentFun = funcImport => {
    return class extends Component {

        componentDidMount(){

            console.log('hello');

            funcImport().then(
                cmp => {
                  
                    this.setState({cmp : cmp.default},()=> console.log(this.state.cmp))
                   
                }
            );

            
        }



        state = {
            cmp : null 
        }

        render(){

            let C = this.state.cmp ;

            return (
                <Aux>
                    
                    {C? <this.state.cmp /> : <CircularProgress/>}
                </Aux>
            )
            
        }
    }
};


export default AsyncComponentFun;