import React from 'react';

import classes from './SideDrawer.module.css';
import Aux from '../../../hoc/Aux';

import Button from '@material-ui/core/Button';

import {NavLink} from 'react-router-dom'

const sideDrawer = props => {



    return (
        <Aux>
            {props.show ?<div className={classes.side}>

            <NavLink
            to="/"
            exact
            activeStyle={{
                borderBottom : '3px solid orange'
            }}
            >
            <Button onClick={props.clicked}>Introduction</Button>
            </NavLink>

            
            <NavLink
            to="/skill"
            activeStyle={{
                borderBottom : '3px solid orange'
            }}
            >
            <Button onClick={props.clicked}>Skill</Button>
            </NavLink>



              <NavLink
              to="/project"
              activeStyle={{
                  borderBottom : '3px solid orange'
              }}
              >
            <Button onClick={props.clicked}>Project</Button>
            </NavLink>
            
            <NavLink
            to="contact"
            activeStyle={{
                borderBottom : '3px solid orange'
            }}
            >
            <Button onClick={props.clicked}>Contact</
            
            Button>
            </NavLink>


            </div> : null}


            
        </Aux>
    )
  
}

export default sideDrawer;